var blockcypher = require('./blockcypher');

var transaction = require('../model/transaction');

var _require = require('./utils'),
    ServiceResponse = _require.ServiceResponse,
    ServiceError = _require.ServiceError,
    InternalError = _require.InternalError;

function pushtx(tx, network) {
  return blockcypher.pushtx(tx, network).then(function (response) {
    return new ServiceResponse(response.status, response.data);
  }).catch(function (error) {
    throw new ServiceError(error.response.status, error.response.data.error);
  });
}

function pullUnspentTransactions(address, network) {
  return blockcypher.pullUnspentTransactions(address, network).then(function (response) {
    if (!response) {
      throw new InternalError("An error has occured. ".concat(address, " has no spendable balance."));
    }

    var data = Array.isArray(response.data) ? response.data : [response.data];
    var txs = [];

    for (var index = 0; index < data.length; index += 1) {
      var tx = data[index];
      var outputDataArray = Array.isArray(tx.outputs) ? tx.outputs : [tx.outputs];
      var outputs = [];

      for (var subindex = 0; subindex < outputDataArray.length; subindex += 1) {
        var output = outputDataArray[subindex];

        if (output.script_type === 'pay-to-pubkey-hash' && output.addresses.length === 1 && output.addresses[0] === address) {
          outputs.push(new transaction.TransactionOutput(subindex, output.script, output.value, address));
        }
      }

      txs.push(new transaction.Transaction(tx.hash, outputs));
    }

    return new ServiceResponse(response.status, txs);
  }).catch(function (error) {
    if (error instanceof InternalError) {
      throw error;
    }

    throw new ServiceError(error.response.status, error.response.data.error);
  });
}

function pullMetadata(network) {
  return blockcypher.pullMetadata(network).then(function (response) {
    return new ServiceResponse(response.status, response.data);
  }).catch(function (error) {
    throw new ServiceError(error.response.status, error.response.data.error);
  });
}

module.exports = {
  pushtx: pushtx,
  pullMetadata: pullMetadata,
  pullUnspentTransactions: pullUnspentTransactions
};